@font-face {
    font-family: 'Century Gothic';
    font-style: normal;
    font-weight: normal;
    src: url(/static/media/CenturyGothic-Regular.cfce6abb.ttf);
}
.nav-calendar {
  position: absolute;
  bottom: 0; }
  @media screen and (max-height: 560px) {
    .nav-calendar {
      display: none !important; } }
  .nav-calendar #calendar {
    width: 248px;
    margin: 0 auto;
    font-size: 5px; }
  .nav-calendar .fc-header-toolbar {
    display: block; }
  .nav-calendar .calendar .canceled {
    background-color: #f5365c; }
  .nav-calendar .calendar .active {
    background-color: #5292B0; }
  .nav-calendar .calendar .complete {
    background-color: #E3885B; }
  .nav-calendar .calendar .fc-toolbar {
    height: unset; }
    .nav-calendar .calendar .fc-toolbar h2 {
      font-size: 1rem; }
  .nav-calendar .calendar .fc-today {
    background-color: #5292b078; }
  .nav-calendar .calendar .fc-icon {
    margin: 15px 15px 0 15px;
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    border: solid #5292B0;
    border-width: 0 0 2px 2px;
    border-radius: 0 !important; }
  .nav-calendar .calendar .fc-icon-chevron-right {
    -webkit-transform: rotate(-135deg);
            transform: rotate(-135deg); }
  .nav-calendar .calendar .fc-icon-chevron-left {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }
  .nav-calendar .calendar .fc-day-number {
    font-size: 11px;
    font-weight: 600;
    color: #67779d; }
  .nav-calendar .calendar .fc-content {
    height: 4px; }
    .nav-calendar .calendar .fc-content .fc-time {
      display: none; }
    .nav-calendar .calendar .fc-content .fc-title {
      padding: 0;
      color: transparent; }
  .nav-calendar .calendar .fc-event,
  .nav-calendar .calendar .fc-event-dot {
    height: 4px; }
  .nav-calendar .fc th {
    padding: 0.75rem 0.75rem;
    font-size: 10px;
    color: gray;
    text-transform: unset; }

.sidenav-header {
  height: auto !important; }

.sidenav .navbar-brand,
.sidenav .navbar-heading {
  padding: 0.5rem !important;
  margin-left: auto; }

.navbar-vertical .navbar-brand-img,
.navbar-vertical .navbar-brand > img {
  max-height: 3.5rem !important; }

.sidenav {
  max-width: 63px !important; }

.g-sidenav-show .sidenav {
  max-width: 250px !important; }

.content-body {
  min-height: calc(100vh - 60px); }

.form__image-slider {
  position: relative; }
  .form__image-slider i.ni {
    position: absolute;
    right: 0;
    font-size: 1.6rem;
    padding: .2rem;
    color: #fff;
    cursor: pointer; }

.form__controller {
  position: relative; }
  .form__controller--error input {
    border: 1px solid #f5365c !important;
    background-color: #f3a4b524; }
  .form__controller input {
    padding-right: 1.4rem; }
  .form__controller .form-control:disabled:hover {
    border: none; }

.form__errors {
  position: absolute;
  right: 0;
  top: calc(50% - 0.5rem);
  width: calc(100% - 1.2rem);
  margin-right: .5rem;
  cursor: pointer; }
  .form__errors:before {
    position: absolute;
    right: 0;
    content: '!';
    color: #f5365c;
    line-height: 1rem;
    font-size: 12px;
    font-weight: bold;
    display: block;
    width: 1.2rem;
    height: 1.2rem;
    border: 2px solid #f5365c;
    border-radius: 50%;
    text-align: center; }
  .form__errors:hover .form__errors__list {
    display: block; }
  .form__errors__list {
    position: absolute;
    bottom: .2rem;
    right: 0;
    display: none;
    padding: .2rem;
    background: #fff;
    z-index: 1;
    border: 1px #f5365c solid;
    border-radius: 5px; }
  .form__errors__item {
    font-size: .8rem;
    color: #f5365c;
    white-space: nowrap; }

.common-loading {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
  display: block;
  justify-content: center;
  text-align: center;
  cursor: wait; }
  .common-loading .fa-spin {
    font-size: 3rem;
    margin-top: 30vh; }

.reset-password .form-control-label,
.reset-password .submit-button,
.reset-password .back {
  font-family: 'Century Gothic', 'Open Sans', sans-serif; }

.create-new-password {
  font-family: 'Century Gothic', 'Open Sans', sans-serif; }
  .create-new-password .auth-header-title {
    font-size: 28px;
    font-weight: bold;
    margin: 0; }
  .create-new-password .auth-header-subtitle {
    font-size: 24px;
    font-weight: bold;
    margin: 20px 0 0; }
  .create-new-password label.custom-control-label.policy-text {
    font-size: 14px;
    text-transform: none;
    height: auto; }
  .create-new-password .policy-checkbox .form__errors {
    top: 7px; }
  .create-new-password .card {
    box-shadow: none; }

