.create-new-password {
    font-family: 'Century Gothic', 'Open Sans', sans-serif;;

    .auth-header-title {
        font-size: 28px;
        font-weight: bold;
        margin: 0;
    }

    .auth-header-subtitle {
        font-size: 24px;
        font-weight: bold;
        margin: 20px 0 0;
    }

    label.custom-control-label.policy-text {
        font-size: 14px;
        text-transform: none;
        height: auto
    }

    .policy-checkbox .form__errors {
        top: 7px;
    }

    .card {
        box-shadow: none;
    }
}