@import '../../scss/variables';

.nav-calendar {
    position: absolute;
    bottom: 0;

    @media screen and (max-height: 560px) {
        display: none !important;
    }

    #calendar {
        width: 248px;
        margin: 0 auto;
        font-size: 5px;
    }

    .fc-header-toolbar {
        display: block;
    }

    .calendar {

        .canceled {
            background-color: $red;
        }

        .active {
            background-color: $blue;
        }

        .complete {
            background-color: $orange;
        }

        .fc-toolbar {
            height: unset;

            & h2 {
                font-size: 1rem;
            }
        }

        .fc-today {
            background-color: #5292b078;
        }

        .fc-icon {
            margin: 15px 15px 0 15px;
            content: "";
            display: block;
            width: 10px;
            height: 10px;
            border: solid #5292B0;
            border-width: 0 0 2px 2px;
            border-radius: 0 !important;
        }

        .fc-icon-chevron-right {
            transform: rotate(-135deg);
        }

        .fc-icon-chevron-left {
            transform: rotate(45deg);
        }

        .fc-day-number {
            font-size: 11px;
            font-weight: 600;
            color: #67779d;
        }

        .fc-content {
            height: 4px;

            .fc-time {
                display: none;
            }

            .fc-title {
                padding: 0;
                color: transparent;
            }
        }

        .fc-event,
        .fc-event-dot {
            height: 4px;
        }
    }

    .fc th {
        padding: 0.75rem 0.75rem;
        font-size: 10px;
        color: gray;
        text-transform: unset;
    }

}