@import '../../scss/variables';

.form {
  &__image-slider {
    position: relative;

    i.ni {
      position: absolute;
      right: 0;
      font-size: 1.6rem;
      padding: .2rem;
      color: $white;
      cursor: pointer;
    }
  }

  &__controller {
    position: relative;

    &--error {
      input {
        border: 1px solid $red !important;
        background-color: #f3a4b524;
      }
    }

    input {
      padding-right: 1.4rem;
    }

    .form-control:disabled {
      &:hover {
        border: none;
      }
    }
  }

  &__errors {
    $root: &;
    position: absolute;
    right: 0;
    top: calc(50% - 0.5rem);
    width: calc(100% - 1.2rem);
    margin-right: .5rem;
    cursor: pointer;

    &:before {
      position: absolute;
      right: 0;
      content: '!';
      color: $red;
      line-height: 1rem;
      font-size: 12px;
      font-weight: bold;
      display: block;
      width: 1.2rem;
      height: 1.2rem;
      border: 2px solid $red;
      border-radius: 50%;
      text-align: center;
    }

    &:hover {
      #{$root}__list {
        display: block;
      }
    }

    &__list {
      position: absolute;
      bottom: .2rem;
      right: 0;
      display: none;
      padding: .2rem;
      background: $white;
      z-index: 1;
      border: 1px $red solid;
      border-radius: 5px;
    }

    &__item {
      font-size: .8rem;
      color: $red;
      white-space: nowrap;
    }
  }
}