@import '../scss/variables';

.btn-register {
    border-radius: 25px;
}

.table-sort {
    th {
        &.sort {
            position: relative;
            cursor: pointer;

            &::after {
                @extend .icon-fa-arrow-down;
                opacity: 0;
                position: absolute;
                right: 3px;
                top: calc(50% - 10px);
            }

            &::before {
                @extend .icon-fa-arrow-up;
                opacity: 0;
                position: absolute;
                right: 3px;
                top: calc(50% - 10px);

            }
        }

        &.sort-desc {
            &::after {
                opacity: 1;
            }
        }

        &.sort-asc {
            &::before {
                opacity: 1;
            }
        }
    }
}

.text-required {
    color: $red;
    font-weight: bold;
}

.avatar {
    object-fit: cover;
}

.custom-avatar {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 130px;
    height: 130px;
    background: linear-gradient(to right bottom, $gray-600 50%, $gray-500 50%);
    cursor: pointer;
}

// Full calendar
.fc {
    .fc-event .fc-time {
        background: rgba(255, 255, 255, 0.3);
        color: #fff;
    }
}

.footer {
    background: transparent;
    padding: $footer-padding-y $footer-padding-x 0 $footer-padding-x;
}

select {
    padding: 10px;
    border: 0px solid;
    background-color: rgba(77, 154, 191, 0.1);
    border-radius: 5px;
    color: #8898aa;
}

//-----Override Full-Calendar-----//

.fc {

    .fc-event-dot {
        background-color: #dbebf2;
    }

    .fc-event {
        border-radius: 0.5rem;
        background-color: #dbebf2;
        margin-top: 0.25rem;
        padding: 5px 0;
    }
}