@import "./variables";

.form-control-label {
    color: $accentText1;
}

.was-validated .form-control:invalid,
.form-control.is-invalid,
.was-validated .custom-select:invalid,
.custom-select.is-invalid {
    background-color: $inputBg;
    border: 1px solid red;
}

.form-control:invalid,
.was-validated .form-control:invalid,
.form-control.is-invalid,
.was-validated .custom-select:invalid,
.custom-select.is-invalid {
    &:focus {
        background-color: $statusCurrent;
        border: 1px solid red;
    }
}

.was-validated .form-control:invalid,
.form-control.is-invalid,
.was-validated .custom-select:invalid,
.custom-select.is-invalid {
    &:hover {
        cursor: pointer;
        background-color: $inputBg;
        border: 1px solid red;
    }
}

.select2-selection {
    background-color: $inputBg!important;
}

.form-control,
.was-validated .form-control:valid,
.form-control.is-valid,
.was-validated .custom-select:valid,
.custom-select.is-valid,
.select2-selection {
    &:focus {
        border: 1px solid $iconForm;
    }
}

.form-control,
.was-validated .form-control:valid,
.form-control.is-valid,
.was-validated .custom-select:valid,
.custom-select.is-valid,
.select2-selection {
    &:hover {
        border: 1px solid $iconForm;
        background-color: $inputBg;
    }
}

.btn-secondary {
    background-color: $accent1_10;
}

.card-header {
    border-bottom: 0px;
}

.invalid-feedback {
    color: red;
    margin-top: 15px;
}

li.active.nav-item {
    background-color: $accent2_10;
    border-right: 4px solid $accent1;
}

li.nav-item:hover {
    background-color: $accent1_10;
}

.navbar-light .navbar-nav .show>.nav-link,
.navbar-light .navbar-nav .active>.nav-link,
.navbar-light .navbar-nav .nav-link.show,
.navbar-light .navbar-nav .nav-link.active {
    color: $accentText1;
}

.navbar-vertical .navbar-nav .nav-link {
    font-weight: 600;
    color: $text1;
}

.main-content {
    background-color: $bg3;

    &__bg-transparent {
      background-color: transparent;
    }
}

small,
.small {
    color: $accentText1;
}

.bg-gradient-default {
    background-color: $accent1 !important;
}

.form-control, .was-validated .form-control:valid, .form-control.is-valid, .was-validated .custom-select:valid, .custom-select.is-valid {
    border: 1px solid transparent;
    background-color: rgba(77, 154, 191, 0.1);
}

td.fc-head-container.fc-widget-header {
    border-top: none;
}

.btn {
    &.disabled, &:disabled {
        cursor: not-allowed;
    }
}

.modal.fade {
    .modal-dialog {
        animation-duration: 100ms;
    }
}

.c-pointer {
    cursor: pointer;
}